
import { mapState } from "vuex";
import axios from "axios";
import { defineComponent } from "vue";
import { Dialog } from "@headlessui/vue";
import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";
import IgButton from "@/components/baseUI/IgButton.vue";

export default defineComponent({
  props: {
    coachingList: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      showDialog: false,
      showSuccessfulDialog: false,
      selectedPaymentName: "",
      paymentData: [
        { name: "Debit Card", selected: false },
        { name: "Bank Transfer", selected: false },
        { name: "Paypal", selected: false },
        { name: "KNET", selected: false },
      ],

      name: "",
      rating: 0,
      description: "",
      language: "",
      sellerName: "",
      sellerInfo: "",
      sellerProfilePicture: null,
      thumb: "",
      cover: "",

      total: 0,
      tax: 0,
      netAmount: 0,
    };
  },
  components: {
    Dialog,
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
    IgButton,
  },
  computed: {
    ...mapState(["apiUrl"]),
    ...mapState(["imageUrl"]),
    checkCoachingList() {
      for (let i = 0; i < this.coachingList.length; i++) {
        /* eslint-disable @typescript-eslint/no-explicit-any*/
        let element: any = this.coachingList[i];
        if (element.selected) {
          return true;
        }
      }
      return false;
    },
  },
  methods: {
    selectPaymentOption(index) {
      this.paymentData.forEach((element) => {
        element.selected = false;
      });
      this.paymentData[index].selected = true;
      this.selectedPaymentName = this.paymentData[index].name;
    },
    successfulDialog() {
      this.showDialog = false;
      this.showSuccessfulDialog = true;
      this.coachingList.forEach((element: any) => {
        element.selected = false;
      });
    },
    getTotal() {
      let total = 0;
      this.coachingList.forEach((element: any) => {
        if (element.selected) {
          let price = parseFloat(element.price);
          if (isNaN(price)) price = 0;
          total = total + Number(price.toFixed(2));
        }
      });
      this.total = total;
      return "";
    },
    getTax() {
      this.tax = Number(((this.total * 18) / 100).toFixed(2));
      return "";
    },
    getNetAmount() {
      this.netAmount = Number((this.total + this.tax).toFixed(2));
      return "";
    },
    openDialog() {
      this.showDialog = true;
      this.getTotal();
      this.getTax();
      this.getNetAmount();
    },
  },
  async created() {
    var response = await axios.get(
      this.apiUrl + "/coach/" + this.$route.params.id
    );
    if (response.data != null && response.data.length > 0) {
      this.name = response.data[0].name;
      this.rating = response.data[0].rating;
      this.description = response.data[0].description;
      this.sellerName = response.data[0].seller_name;
      this.sellerInfo = response.data[0].seller_info;
      this.sellerProfilePicture = response.data[0].seller_profile_picture;
      this.thumb = response.data[0].thumb;
      this.cover = response.data[0].cover;
    }
  },
});
