<template>
  <Menu as="div" class="relative inline-block text-left">
    <div>
      <MenuButton class="w-full" @click="setPropsDate()">
        <div class="select-input">
          <span class="my-auto" v-if="selectedDateString != ''">{{
            selectedDateString
          }}</span>
          <span class="my-auto placeholder" v-else>{{ placeholder }}</span>
          <span class="my-auto ml-auto">
            <svg
              width="0.8rem"
              height="0.8rem"
              aria-hidden="true"
              focusable="false"
              data-prefix="fas"
              data-icon="chevron-down"
              class="svg-inline--fa fa-chevron-down fa-w-14"
              role="img"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 448 512"
            >
              <path
                fill="currentColor"
                d="M207.029 381.476L12.686 187.132c-9.373-9.373-9.373-24.569 0-33.941l22.667-22.667c9.357-9.357 24.522-9.375 33.901-.04L224 284.505l154.745-154.021c9.379-9.335 24.544-9.317 33.901.04l22.667 22.667c9.373 9.373 9.373 24.569 0 33.941L240.971 381.476c-9.373 9.372-24.569 9.372-33.942 0z"
              ></path>
            </svg>
          </span>
        </div>
      </MenuButton>
    </div>
    <transition
      enter-active-class="transition duration-100 ease-out"
      enter-from-class="transform scale-95 opacity-0"
      enter-to-class="transform scale-100 opacity-100"
      leave-active-class="transition duration-75 ease-in"
      leave-from-class="transform scale-100 opacity-100"
      leave-to-class="transform scale-95 opacity-0"
    >
      <MenuItems
        class="
          absolute
          right-0
          w-56
          origin-top-right
          bg-black
          rounded-md
          shadow-lg
          ring-1 ring-black ring-opacity-5
          focus:outline-none
          date-picker-block
        "
      >
        <MenuItem :disabled="true">
          <div>
            <div class="flex" style="justify-content: center">
              <div class="mx-2">
                <div class="flex w-full">
                  <a
                    href="javascript:void(0)"
                    class="m-auto"
                    @click="getPervDate()"
                  >
                    <svg
                      width="0.8rem"
                      height="0.8rem"
                      viewBox="0 0 10 8"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M9.95898 5.88208L8.78463 7.0564L4.97945 3.25122L1.17427 7.0564L-8.01086e-05 5.88208L4.97945 0.902551L9.95898 5.88208Z"
                        fill="white"
                      />
                    </svg>
                  </a>
                </div>
                <div class="date-btn">
                  {{ dd }}
                </div>
                <div class="flex w-full">
                  <a
                    href="javascript:void(0)"
                    class="m-auto"
                    @click="getNextDate()"
                  >
                    <svg
                      width="0.8rem"
                      height="0.8rem"
                      viewBox="0 0 10 8"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M9.95898 2.0769L8.78463 0.902588L4.97945 4.70777L1.17427 0.902588L-8.01086e-05 2.0769L4.97945 7.05643L9.95898 2.0769Z"
                        fill="white"
                      />
                    </svg>
                  </a>
                </div>
              </div>
              <div class="mx-2">
                <div class="flex w-full">
                  <a
                    href="javascript:void(0)"
                    class="m-auto"
                    @click="getPervMonth()"
                  >
                    <svg
                      width="0.8rem"
                      height="0.8rem"
                      viewBox="0 0 10 8"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M9.95898 5.88208L8.78463 7.0564L4.97945 3.25122L1.17427 7.0564L-8.01086e-05 5.88208L4.97945 0.902551L9.95898 5.88208Z"
                        fill="white"
                      />
                    </svg>
                  </a>
                </div>
                <div class="date-btn">
                  {{ mm }}
                </div>
                <div class="flex w-full">
                  <a
                    href="javascript:void(0)"
                    class="m-auto"
                    @click="getNextMonth()"
                  >
                    <svg
                      width="0.8rem"
                      height="0.8rem"
                      viewBox="0 0 10 8"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M9.95898 2.0769L8.78463 0.902588L4.97945 4.70777L1.17427 0.902588L-8.01086e-05 2.0769L4.97945 7.05643L9.95898 2.0769Z"
                        fill="white"
                      />
                    </svg>
                  </a>
                </div>
              </div>
              <div class="mx-2">
                <div class="flex w-full">
                  <a
                    href="javascript:void(0)"
                    class="m-auto"
                    @click="getPervYear()"
                  >
                    <svg
                      width="0.8rem"
                      height="0.8rem"
                      viewBox="0 0 10 8"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M9.95898 5.88208L8.78463 7.0564L4.97945 3.25122L1.17427 7.0564L-8.01086e-05 5.88208L4.97945 0.902551L9.95898 5.88208Z"
                        fill="white"
                      />
                    </svg>
                  </a>
                </div>
                <div class="date-btn">
                  {{ yy }}
                </div>
                <div class="flex w-full">
                  <a
                    href="javascript:void(0)"
                    class="m-auto"
                    @click="getNextYear()"
                  >
                    <svg
                      width="0.8rem"
                      height="0.8rem"
                      viewBox="0 0 10 8"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M9.95898 2.0769L8.78463 0.902588L4.97945 4.70777L1.17427 0.902588L-8.01086e-05 2.0769L4.97945 7.05643L9.95898 2.0769Z"
                        fill="white"
                      />
                    </svg>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </MenuItem>
        <MenuItem @click="setDate()">
          <div>
            <div class="mt-8 mb-1">
              <IgButton
                class="ig-full ig-font-small px-10 ig-add-border"
                style="height: 32px"
                buttonName="select"
                textColor="ig-white-text"
                @click="setDate()"
              />
            </div>
          </div>
        </MenuItem>
      </MenuItems>
    </transition>
  </Menu>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import { emitter } from "@/main";
export default defineComponent({
  props: ["propsDate", "placeholder", "changeDate"],
  data() {
    return {
      selectedDateString: "",
      selectedDate: "",
      dd: "",
      mm: "",
      yy: "",
      monthNames: [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ],
    };
  },
  components: {
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
    IgButton,
  },
  methods: {
    setPropsDate() {
      if (this.propsDate != "") {
        this.selectedDate = new Date(this.propsDate).toISOString();
        this.selectedDateString = this.mm + " " + this.dd + ", " + this.yy;
        this.dd = new Date(this.propsDate).getDate().toString();
        this.mm = this.monthNames[new Date(this.propsDate).getMonth()];
        this.yy = new Date(this.propsDate).getFullYear().toString();
      }
    },
    getPervDate() {
      var date =
        this.selectedDate != "" ? new Date(this.selectedDate) : new Date();
      if (date === undefined) date = new Date();
      var newDate = new Date(date.setDate(date.getDate() - 1));

      this.dd = newDate.getDate().toString();
      this.mm = this.monthNames[newDate.getMonth()];
      this.yy = newDate.getFullYear().toString();
      this.selectedDate = newDate.toISOString();
    },
    getNextDate() {
      var date =
        this.selectedDate != "" ? new Date(this.selectedDate) : new Date();
      if (date === undefined) date = new Date();
      var newDate = new Date(date.setDate(date.getDate() + 1));

      this.dd = newDate.getDate().toString();
      this.mm = this.monthNames[newDate.getMonth()];
      this.yy = newDate.getFullYear().toString();
      this.selectedDate = newDate.toISOString();
    },

    getPervMonth() {
      var date =
        this.selectedDate != "" ? new Date(this.selectedDate) : new Date();
      if (date === undefined) date = new Date();
      var newDate = new Date(date.setMonth(date.getMonth() - 1));

      this.dd = newDate.getDate().toString();
      this.mm = this.monthNames[newDate.getMonth()];
      this.yy = newDate.getFullYear().toString();
      this.selectedDate = newDate.toISOString();
    },
    getNextMonth() {
      var date =
        this.selectedDate != "" ? new Date(this.selectedDate) : new Date();
      if (date === undefined) date = new Date();
      var newDate = new Date(date.setMonth(date.getMonth() + 1));

      this.dd = newDate.getDate().toString();
      this.mm = this.monthNames[newDate.getMonth()];
      this.yy = newDate.getFullYear().toString();
      this.selectedDate = newDate.toISOString();
    },

    getPervYear() {
      var date =
        this.selectedDate != "" ? new Date(this.selectedDate) : new Date();
      if (date === undefined) date = new Date();
      var newDate = new Date(date.setFullYear(date.getFullYear() - 1));

      this.dd = newDate.getDate().toString();
      this.mm = this.monthNames[newDate.getMonth()];
      this.yy = newDate.getFullYear().toString();
      this.selectedDate = newDate.toISOString();
    },
    getNextYear() {
      var date =
        this.selectedDate != "" ? new Date(this.selectedDate) : new Date();
      if (date === undefined) date = new Date();
      var newDate = new Date(date.setFullYear(date.getFullYear() + 1));

      this.dd = newDate.getDate().toString();
      this.mm = this.monthNames[newDate.getMonth()];
      this.yy = newDate.getFullYear().toString();
      this.selectedDate = newDate.toISOString();
    },

    setDate() {
      this.selectedDateString = this.mm + " " + this.dd + ", " + this.yy;
      this.$emit("changeDate", this.selectedDate);
    },
  },
  created() {
    this.selectedDate = new Date().toISOString();
    this.dd = new Date().getDate().toString();
    this.mm = this.monthNames[new Date().getMonth()];
    this.yy = new Date().getFullYear().toString();
    this.selectedDateString = "";

    emitter.on("cleanDatePickerDate", () => {
      this.selectedDate = "";
      this.selectedDateString = "";
    });
  },
  deactivated() {
    emitter.off("cleanDatePickerDate");
  },
});
</script>
