<template>
  <div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-3">
    <IconWithName
      v-for="(item, index) in spaceData"
      :key="index"
      :image="item.thumb"
      defaultImageType="team"
      :name="item.name"
      :subLine="item.members_count + ' ' + $t('userPage.members')"
    />
  </div>
  <div class="mt-3 text-center" v-if="spaceData.length < spaceTotal">
    <IgButton
      class="ig-small px-3 ig-font-small ig-add-border"
      buttonName="see_more"
      textColor="ig-white-text"
      @click="getSpaces(spacePage + 1)"
    />
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import IconWithName from "@/components/baseUI/IconWithName.vue";
import axios from "axios";
import { mapState } from "vuex";
import IgButton from "@/components/baseUI/IgButton.vue";

export default defineComponent({
  data() {
    return {
      spacePage: 1,
      spaceTotal: 0,
      spaceData: [],
    };
  },
  components: {
    IconWithName,
    IgButton,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    async getSpaces(page) {
      var response = await axios.get(this.apiUrl + "/profile/spaces", {
        params: {
          page: page,
        },
        headers: {
          authorization: "Bearer " + localStorage.getItem("access_token"),
        },
      });
      if (response.data != null) {
        this.spacePage = page;
        this.spaceTotal = response.data.count;
        if (page == 1) this.spaceData = [];
        this.spaceData = this.spaceData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getSpaces(1);
  },
});
</script>
