
import { defineComponent } from "vue";
import IconWithName from "@/components/baseUI/IconWithName.vue";

export default defineComponent({
  props: {
    members: {
      type: Array,
      required: true,
    },
  },
  components: {
    IconWithName,
  },
  methods: {
    getSubLine(item) {
      var currentDate = new Date();
      var date = new Date(item.create);
      var months = 0;
      months = (currentDate.getFullYear() - date.getFullYear()) * 12;
      months -= date.getMonth();
      months += currentDate.getMonth();
      months = months <= 0 ? 0 : months;

      return (
        this.$t("teamPage.join") +
        " " +
        months +
        " " +
        this.$t("teamPage.month_ago")
      );
    },
    getTagTex(item) {
      if (item.captain) {
        return this.$t("teamPage.captain");
      } else if (item.vice_captain) {
        return this.$t("teamPage.vice_captain");
      } else {
        return "";
      }
    },
  },
});
