<template>
  <div class="grid grid-cols-1 md:grid-cols-2 gap-8">
    <div
      class="grid grid-rows-2 statistics-block bg-black"
      v-for="(item, index) in statisticsData"
      :key="index"
    >
      <div class="md:flex md:mb-5">
        <div
          class="statistic-img-bock"
          :style="{ backgroundImage: 'url(' + item.thumb + ')' }"
        ></div>
        <div class="statistic-block mt-4 md:mt-auto">
          <p class="truncate">{{ item.name }}</p>
          <button>{{ $t("userPage.tier_lll") }}</button>
        </div>
      </div>
      <div class="grid grid-cols-2 md:grid-cols-4 gap-3">
        <div v-if="item.played != null">
          <small class="color-gray">{{ $t("userPage.played") }}</small>
          <p>
            <b>{{ item.played + "  " + $t("userPage.times") }}</b>
          </p>
        </div>
        <div v-if="item.matches != null">
          <small class="color-gray">{{ $t("userPage.matches") }}</small>
          <p>
            <b>{{ item.matches + " " + $t("userPage.times") }}</b>
          </p>
        </div>
        <div v-if="item.play_rate != null">
          <small class="color-gray">{{ $t("userPage.played_rate") }}</small>
          <p>
            <b>{{ play_rate + "%" }}</b>
          </p>
        </div>
        <div v-if="item.win_rate != null">
          <small class="color-gray">{{ $t("userPage.win_rate") }}</small>
          <p>
            <b>{{ win_rate + "%" }}</b>
          </p>
        </div>
        <div v-if="item.pick_rate != null">
          <small class="color-gray">{{ $t("userPage.pick_rate") }}</small>
          <p>
            <b>{{ item.pick_rate + "%" }}</b>
          </p>
        </div>
        <div v-if="item.ban_rate != null">
          <small class="color-gray">{{ $t("userPage.ban_rate") }}</small>
          <p>
            <b>{{ item.ban_rate + "%" }}</b>
          </p>
        </div>
        <div class="col-span-2" v-if="item.KDA != null">
          <small class="color-gray">{{ $t("userPage.KDA") }}</small>
          <p>
            <b>{{ item.KDA }}</b>
            <!-- <b
              ><span class="color-primary">6.5</span> /
              <span class="color-red">5.4</span> /
              <span class="color-yellow">7.3</span></b
            > -->
          </p>
        </div>
      </div>
    </div>
    <div
      class="col-span-1 md:col-span-2 mt-3 text-center"
      v-if="statisticsData.length < statisticsTotal"
    >
      <IgButton
        class="ig-small px-3 ig-font-small ig-add-border"
        buttonName="see_more"
        textColor="ig-white-text"
        @click="getStatistics(statisticsPage + 1)"
      />
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import axios from "axios";
import IgButton from "@/components/baseUI/IgButton.vue";

import { mapState } from "vuex";

export default defineComponent({
  data() {
    return {
      statisticsPage: 1,
      statisticsTotal: 0,
      statisticsData: [],
    };
  },
  components: {
    IgButton,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    async getStatistics(page) {
      var response = await axios.get(this.apiUrl + "/profile/statistics", {
        params: {
          page: page,
        },
        headers: {
          authorization: "Bearer " + localStorage.getItem("access_token"),
        },
      });
      if (response.data != null) {
        this.statisticsPage = page;
        this.statisticsTotal = response.data.count;
        response.data.results.forEach((element) => {
          element.selected = false;
        });
        if (page == 1) this.statisticsData = [];
        this.statisticsData = this.statisticsData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getStatistics(1);
  },
});
</script>
