<template>
  <router-link to="/tournament">
    <div class="md:flex available-tournaments mb-8">
      <div
        class="img-block"
        :style="{ backgroundImage: 'url(' + data.cover + ')' }"
      ></div>
      <div class="content mt-auto w-100">
        <img :src="data.icon" class="mb-2" alt="game-icon" />
        <p class="mb-2 truncate" style="max-width: 16rem">
          <b>{{ data.name }}</b>
        </p>
        <p class="mb-2 color-gray">
          <small
            >{{
              data.tournament_format +
              " &#8226; " +
              data.team_size +
              " &#8226; $" +
              data.custom_price +
              " &#8226; " +
              (data.team_maximum - data.team_minimum > 0
                ? data.team_maximum - data.team_minimum + " slots"
                : "")
            }}
          </small>
        </p>
        <div class="flex">
          <div class="tag">{{ displayDate(data.created) }}</div>
        </div>
      </div>
    </div>
  </router-link>
</template>
<script lang="ts">
import { defineComponent } from "vue";
export default defineComponent({
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  methods: {
    displayDate(value) {
      if (value == null || value == "") return "";

      return new Date(value).toLocaleString("en-GB", {
        day: "numeric",
        month: "long",
        year: "numeric",
        hour: "numeric",
        minute: "numeric",
      });
    },
  },
});
</script>
<style lang="scss" scoped>
.available-tournaments {
  .img-block {
    margin-right: 1.5rem;
    border-radius: 10px;
    min-width: 20rem;
    max-width: 20rem;
    min-height: 12rem;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .content {
    img {
      width: 2.8rem;
      height: 2.8rem;
      border-radius: 1.4rem;
    }
    .tag {
      font-size: 0.8rem;
      padding: 0.3rem 0.5rem;
      background: #3d3f4d;
      border-radius: 5px;
    }
  }
}
</style>
