
import { defineComponent } from "vue";
import TeamFinder from "@/components/teamFinder/teamFinder.vue";
import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import IgToggleBtn from "@/components/baseUI/IgToggleBtn.vue";

export default defineComponent({
  data() {
    return {
      selectedRoleName: "",
      rolesData: [
        { name: "Captain", selected: false },
        { name: "Vice Captain", selected: false },
      ],

      selectedCountryName: "",
      countryData: [
        { name: "India", selected: false },
        { name: "USA", selected: false },
        { name: "UK", selected: false },
      ],

      selectedLanguageName: "",
      languageData: [
        { name: "English", selected: false },
        { name: "Arbi", selected: false },
      ],

      min_rank: "",
      max_rank: "",
      show_18_only: false,

      applyFilter: false,
    };
  },
  components: {
    TeamFinder,
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
    IgButton,
    IgToggleBtn,
  },
  computed: {
    checkFilterApply() {
      if (
        this.selectedRoleName != "" ||
        this.selectedCountryName != "" ||
        this.selectedLanguageName != "" ||
        this.min_rank != "" ||
        this.max_rank != "" ||
        this.show_18_only
      ) {
        return true;
      }
      return false;
    },
  },
  methods: {
    selectRoles(index) {
      this.rolesData.forEach((element) => {
        element.selected = false;
      });
      this.rolesData[index].selected = true;
      this.selectedRoleName = this.rolesData[index].name;
    },
    selectCountry(index) {
      this.countryData.forEach((element) => {
        element.selected = false;
      });
      this.countryData[index].selected = true;
      this.selectedCountryName = this.countryData[index].name;
    },
    selectLanguage(index) {
      this.languageData.forEach((element) => {
        element.selected = false;
      });
      this.languageData[index].selected = true;
      this.selectedLanguageName = this.languageData[index].name;
    },
    cleanFilter() {
      this.applyFilter = false;
      this.selectedRoleName = "";
      this.rolesData.forEach((element) => {
        element.selected = false;
      });
      this.selectedCountryName = "";
      this.countryData.forEach((element) => {
        element.selected = false;
      });
      this.selectedLanguageName = "";
      this.languageData.forEach((element) => {
        element.selected = false;
      });
      this.min_rank = "";
      this.max_rank = "";
      this.show_18_only = false;
    },
  },
});
