
import { defineComponent } from "vue";
import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";
import OverView from "@/components/team/ovierview.vue";
import Member from "@/components/team/member.vue";
import Statistics from "@/components/team/statistics.vue";
import { Dialog } from "@headlessui/vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import axios from "axios";
import { mapState } from "vuex";
export default defineComponent({
  data() {
    return {
      selectedTab: "overview",
      showJoinDialog: false,
      showLeaveDialog: false,
      showRequestSentBtn: false,
      showInviteBtn: false,

      friendData: [],

      name: "",
      rank: "",
      created: "",
      thumb: null,
      cover: null,
      about: "",
      members: [],
    };
  },
  components: {
    OverView,
    Member,
    Statistics,
    Dialog,
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
    IgButton,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    changeTab(key: string) {
      this.selectedTab = key;
    },
    sendRequest() {
      this.showRequestSentBtn = true;
      this.showInviteBtn = true;
      this.showJoinDialog = false;
    },
    leavTeam() {
      this.showInviteBtn = false;
      this.showRequestSentBtn = false;
      this.showLeaveDialog = false;
    },
    sednInvitaction() {
      /* eslint-disable @typescript-eslint/no-explicit-any*/
      this.friendData.forEach((element: any) => {
        element.selected = false;
      });
    },
    displayDate(value) {
      if (value == null || value == "") return "";

      return new Date(value).toLocaleString("en-GB", {
        day: "2-digit",
        month: "long",
        year: "numeric",
      });
    },
  },
  async created() {
    var result = await axios.get(this.apiUrl + "/friends");
    if (result.data && result.data.results && result.data.results.length > 0) {
      result.data.results.forEach((element) => {
        element.selected = false;
      });
      this.friendData = result.data.results;
    }

    var response = await axios.get(
      this.apiUrl + "/teams/" + this.$route.params.id
    );
    if (
      response.data &&
      response.data.results &&
      response.data.results.length > 0
    ) {
      this.name = response.data.results[0].name;
      this.rank = response.data.results[0].rank;
      this.about = response.data.results[0].about;
      this.created = response.data.results[0].created;
      this.thumb = response.data.results[0].thumb;
      this.cover = response.data.results[0].cover;
      this.members = response.data.results[0].members;
    }
  },
});
