<template>
  <div>
    <div
      class="img-block"
      :style="{ backgroundImage: 'url(' + data.cover + ')' }"
    ></div>
    <div class="content">
      <div class="flex my-4">
        <img :src="data.icon" class="mb-2 game-icon" />
        <div class="tag my-auto ml-auto">{{ displayDate(data.created) }}</div>
      </div>
      <p class="mb-2 truncate">
        <b>{{ data.name }}</b>
      </p>
      <p class="mb-2 color-gray">
        <small
          >{{
            data.tournament_format +
            " &#8226; " +
            data.team_size +
            " &#8226; $" +
            data.custom_price +
            " &#8226; " +
            (data.team_maximum - data.team_minimum > 0
              ? data.team_maximum - data.team_minimum + " slots"
              : "")
          }}
        </small>
      </p>
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  methods: {
    displayDate(value) {
      if (value == null || value == "") return "";

      return new Date(value).toLocaleString("en-GB", {
        day: "numeric",
        month: "long",
        year: "numeric",
        hour: "numeric",
        minute: "numeric",
      });
    },
  },
});
</script>
<style lang="scss" scoped>
.img-block {
  margin-right: 1.5rem;
  border-radius: 10px;
  width: 100%;
  min-height: 11rem;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.game-icon {
  width: 2.8rem;
  height: 2.8rem;
  border-radius: 1.4rem;
}
.tag {
  font-size: 0.8rem;
  padding: 0.3rem 0.5rem;
  background: #282832;
  border-radius: 5px;
}
</style>
