
import { defineComponent } from "vue";
import IgFilter from "@/components/baseUI/IgFilter.vue";
import AvailableTournamentHorizontal from "@/components/availableTournament/AvailableTournamentHorizontal.vue";
import axios from "axios";
import { mapState } from "vuex";
export default defineComponent({
  data() {
    return {
      keyword: "",

      tournamentPage: 1,
      tournamentTotal: 0,
      tournamentData: [],
    };
  },
  components: {
    IgFilter,
    AvailableTournamentHorizontal,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    getSearchData() {
      if (this.keyword.length % 3 == 0) {
        this.getTournament(1);
      }
    },
    cleanSearchData() {
      this.keyword = "";
      this.getTournament(1);
    },
    async getTournament(page) {
      var response = await axios.get(this.apiUrl + "/tournament/", {
        params: {
          page: page,
          keyword: this.keyword,
        },
      });
      if (response.data != null && response.data.results) {
        this.tournamentPage = page;
        this.tournamentTotal = response.data.count;
        if (page == 1) this.tournamentData = [];
        this.tournamentData = this.tournamentData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getTournament(1);
  },
});
