
import { defineComponent } from "vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import Tournaments from "@/components/gamePage/Tournaments.vue";
import Leaderboards from "@/components/gamePage/Leaderboards.vue";
import TeamFinder from "@/components/gamePage/TeamFinder.vue";
import Overview from "@/components/gamePage/Overview.vue";

import { Dialog } from "@headlessui/vue";
import axios from "axios";
import { mapState } from "vuex";
export default defineComponent({
  data() {
    return {
      selectedTab: "overview",
      showGameDescriptions: false,
      showAddGameDialog: false,

      thumb: null,
      name: "",
      shortDescription: "",
      description: "",
    };
  },
  components: {
    IgButton,
    Tournaments,
    Leaderboards,
    TeamFinder,
    Overview,
    Dialog,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    changeTab(key: string) {
      this.selectedTab = key;
    },
  },
  async created() {
    console.log("demo");
    var response = await axios.get(
      this.apiUrl + "/games/" + this.$route.params.id
    );
    if (response.data) {
      this.name = response.data[0].name;
      this.shortDescription = response.data[0].description.substring(0, 80);
      this.description = response.data[0].description;
      this.thumb = response.data[0].thumb;
    }
  },
});
