
import { defineComponent } from "vue";
export default defineComponent({
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  methods: {
    displayDate(value) {
      if (value == null || value == "") return "";

      return new Date(value).toLocaleString("en-GB", {
        day: "numeric",
        month: "long",
        year: "numeric",
        hour: "numeric",
        minute: "numeric",
      });
    },
  },
});
