<template>
  <div
    class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 px-2 xl:px-0"
  >
    <TournamentBlock
      v-for="(item, index) in tournamentData"
      :key="index"
      :image="item.thumb != null ? item.thumb : ''"
      :name="item.name"
      :subLine="$t('on') + ' ' + displayDate(item.created)"
      :tagText="item.status"
    />
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import TournamentBlock from "@/components/baseUI/TournamentBlock.vue";
import axios from "axios";
import { mapState } from "vuex";

export default defineComponent({
  data() {
    return {
      tournamentPage: 1,
      tournamentTotal: 0,
      tournamentData: [],
    };
  },
  components: {
    TournamentBlock,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    displayDate(value) {
      if (value == null || value == "") return "";

      return new Date(value).toLocaleString("en-GB", {
        day: "numeric",
        month: "long",
        year: "numeric",
        hour: "numeric",
        minute: "numeric",
      });
    },
    async getTournaments(page) {
      var response = await axios.get(this.apiUrl + "/profile/tournaments", {
        params: {
          page: page,
        },
        headers: {
          authorization: "Bearer " + localStorage.getItem("access_token"),
        },
      });
      if (response.data != null) {
        this.tournamentPage = page;
        this.tournamentTotal = response.data.count;
        response.data.results.forEach((element) => {
          element.selected = false;
        });
        if (page == 1) this.tournamentData = [];
        this.tournamentData = this.tournamentData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getTournaments(1);
  },
});
</script>
