<template>
  <div class="grid grid-cols-1 members">
    <div>
      <div class="mb-6">
        <p>
          <b>{{ $t("teamPage.members") }}</b>
        </p>
      </div>
      <div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-3">
        <IconWithName
          v-for="(item, index) in members"
          :key="index"
          :image="item.profile_picture != null ? item.profile_picture : ''"
          defaultImageType="user"
          :name="item.name"
          :subLine="getSubLine(item)"
          :tagText="getTagTex(item)"
        />
      </div>
    </div>
    <div class="mt-6">
      <div class="mb-6">
        <p>
          <b>{{ $t("teamPage.coaches") }}</b>
        </p>
      </div>
      <div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-3">
        <IconWithName
          image=""
          defaultImageType="user"
          name="Ivan Magalhaes"
          :subLine="$t('teamPage.join') + ' 4 ' + $t('teamPage.month_ago')"
        />
      </div>
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import IconWithName from "@/components/baseUI/IconWithName.vue";

export default defineComponent({
  props: {
    members: {
      type: Array,
      required: true,
    },
  },
  components: {
    IconWithName,
  },
  methods: {
    getSubLine(item) {
      var currentDate = new Date();
      var date = new Date(item.create);
      var months = 0;
      months = (currentDate.getFullYear() - date.getFullYear()) * 12;
      months -= date.getMonth();
      months += currentDate.getMonth();
      months = months <= 0 ? 0 : months;

      return (
        this.$t("teamPage.join") +
        " " +
        months +
        " " +
        this.$t("teamPage.month_ago")
      );
    },
    getTagTex(item) {
      if (item.captain) {
        return this.$t("teamPage.captain");
      } else if (item.vice_captain) {
        return this.$t("teamPage.vice_captain");
      } else {
        return "";
      }
    },
  },
});
</script>
