
import { defineComponent } from "vue";
import { mapState } from "vuex";
import "vue-select/dist/vue-select.css";
import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";
import AvailableCoachings from "@/components/availableCoaching/availableCoaching.vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import axios from "axios";
// import vSelect from 'vue-select';
export default defineComponent({
  data() {
    return {
      keyword: "",
      showSortMenu: false,
      applySort: false,
      sortData: [
        {
          title: this.$t("coachingPage.most_popular"),
          key: "most_popular",
          selected: false,
        },
        {
          title: this.$t("coachingPage.newly_added"),
          key: "newly_added",
          selected: false,
        },
        {
          title: this.$t("coachingPage.least_added"),
          key: "least_added",
          selected: false,
        },
        {
          title: this.$t("coachingPage.highest_lowest_price"),
          key: "highprice_lowprice",
          selected: false,
        },
        {
          title: this.$t("coachingPage.lowest_highest_price"),
          key: "lowprice_highprice",
          selected: false,
        },
        {
          title: this.$t("coachingPage.most_rated"),
          key: "most_rated",
          selected: false,
        },
        {
          title: this.$t("coachingPage.least_rated"),
          key: "least_rated",
          selected: false,
        },
      ],

      gamePage: 1,
      gameTotal: 0,
      selectedGameName: "",
      gameData: [{ id: "", name: "", selected: false }],

      selectedLanguageName: "",
      languageData: [
        { name: "English", selected: false },
        { name: "Arbi", selected: false },
      ],

      selectedRatingName: "",
      ratingData: [
        {
          name: "1 " + this.$t("coachingPage.stars"),
          stars: 1,
          selected: false,
        },
        {
          name: "2 " + this.$t("coachingPage.stars"),
          stars: 2,
          selected: false,
        },
        {
          name: "3 " + this.$t("coachingPage.stars"),
          stars: 3,
          selected: false,
        },
        {
          name: "4 " + this.$t("coachingPage.stars"),
          stars: 4,
          selected: false,
        },
        {
          name: "5 " + this.$t("coachingPage.stars"),
          stars: 5,
          selected: false,
        },
      ],

      applyFilter: false,

      start_range: "",
      end_range: "",

      coachingPage: 1,
      coachingTotal: 0,
      coachingData: [],
    };
  },
  components: {
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
    AvailableCoachings,
    IgButton,
    // vSelect
  },
  computed: {
    ...mapState(["apiUrl"]),
    checkSortBtn() {
      var result = false;
      this.sortData.forEach((element) => {
        if (element.selected && !result) result = true;
      });
      return result;
    },
    checkFilterApply() {
      if (
        this.selectedGameName != "" ||
        this.selectedLanguageName != "" ||
        this.selectedRatingName != "" ||
        this.start_range != "" ||
        this.end_range != ""
      ) {
        return true;
      }
      return false;
    },
  },
  methods: {
    selectGame(item) {
      this.gameData.forEach((element) => {
        element.selected = false;
      });
      item.selected = true;
      this.selectedGameName = item.name;
    },
    selectLanguage(index) {
      this.languageData.forEach((element) => {
        element.selected = false;
      });
      this.languageData[index].selected = true;
      this.selectedLanguageName = this.languageData[index].name;
    },
    selectRating(index) {
      this.ratingData.forEach((element) => {
        element.selected = false;
      });
      this.ratingData[index].selected = true;
      this.selectedRatingName = this.ratingData[index].name;
    },

    getSearchData() {
      if (this.keyword.length % 3 == 0) {
        this.getCoach(1);
      }
    },
    cleanSearchData() {
      this.keyword = "";
      this.getCoach(1);
    },
    cleanSortBy() {
      this.sortData.forEach((element) => {
        element.selected = false;
      });
      this.applySort = false;
      this.getCoach(1);
    },
    getSortData() {
      this.applySort = true;
      this.getCoach(this.coachingPage);
    },
    getFilterData() {
      this.applyFilter = true;
      this.getCoach(this.coachingPage);
    },
    cleanFilter() {
      this.applyFilter = false;
      this.selectedGameName = "";
      this.gameData.forEach((element) => {
        element.selected = false;
      });
      this.selectedLanguageName = "";
      this.languageData.forEach((element) => {
        element.selected = false;
      });
      this.selectedRatingName = "";
      this.ratingData.forEach((element) => {
        element.selected = false;
      });
      this.start_range = "";
      this.end_range = "";
      this.getCoach(1);
    },

    async getGame(page) {
      var response = await axios.get(this.apiUrl + "/games/", {
        params: {
          page: page,
        },
      });
      if (response.data != null) {
        this.gamePage = page;
        this.gameTotal = response.data.count;
        response.data.results.forEach((element) => {
          element.selected = false;
        });
        if (page == 1) this.gameData = [];
        this.gameData = this.gameData.concat(response.data.results);
      }
    },

    async getCoach(page) {
      var url =
        this.apiUrl +
        "/coach/?page=" +
        page +
        "&keyword=" +
        this.keyword +
        "&price_start=" +
        this.start_range +
        "&price_end" +
        this.end_range;

      this.sortData.forEach((element) => {
        if (element.selected) url += "&sort=" + element.key;
      });

      this.gameData.forEach((element) => {
        if (element.selected) url += "&game_uuid=" + element.id;
      });

      this.languageData.forEach((element) => {
        if (element.selected) url += "&language=" + element.name;
      });

      this.ratingData.forEach((element) => {
        if (element.selected) url += "&rating=" + element.stars;
      });

      var response = await axios.get(url);
      if (response.data) {
        this.coachingPage = page;
        this.coachingTotal = response.data.count;
        if (page == 1) this.coachingData = [];
        this.coachingData = this.coachingData.concat(response.data);
      }
    },
  },
  async created() {
    await this.getCoach(1);
    await this.getGame(1);
  },
});
