
import { defineComponent } from "vue";
import { mapState } from "vuex";
import axios from "axios";
import SellerInfo from "@/components/sellerInfo/sellerInfo.vue";
import PurchaseConfirmation from "@/components/coachingDetails/purchaseConfirmation.vue";
export default defineComponent({
  data() {
    return {
      name: "",
      rating: 0,
      description: "",
      language: "",
      sellerName: "",
      sellerInfo: "",
      sellerProfilePicture: null,
      thumb: "",
      cover: "",
      coachingList: [],
    };
  },
  components: {
    SellerInfo,
    PurchaseConfirmation,
  },
  computed: {
    ...mapState(["apiUrl"]),
    ...mapState(["imageUrl"]),
  },
  methods: {
    changeData(item) {
      console.log(item);
    },
  },
  async created() {
    var response = await axios.get(
      this.apiUrl + "/coach/" + this.$route.params.id
    );
    if (response.data != null && response.data.length > 0) {
      this.name = response.data[0].name;
      this.rating = response.data[0].rating;
      this.description = response.data[0].description;
      this.language = response.data[0].language;
      this.sellerName = response.data[0].seller_name;
      this.sellerInfo = response.data[0].seller_info;
      this.sellerProfilePicture = response.data[0].seller_profile_picture;
      this.thumb = response.data[0].thumb;
      this.cover = response.data[0].cover;
      response.data[0].courses.forEach((element) => {
        element.selected = false;

        let _coach_info =
          element.coach_info.length > 120
            ? element.coach_info.substring(0, 120) + "..."
            : element.coach_info;
        element._coach_info = _coach_info;
        element.show_more = element.coach_info.length > 120 ? true : false;
      });
      this.coachingList = response.data[0].courses;
    }
  },
});
