<script lang="ts">
import AvailableTournamentVertical from "@/components/availableTournament/AvailableTournamentVertical.vue";
import CoachingList from "@/components/coachingList/CoachingList.vue";
import { defineComponent } from "vue";
import { Dialog } from "@headlessui/vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import axios from "axios";
import { mapState } from "vuex";
export default defineComponent({
  data() {
    return {
      showAboutDialog: false,
      showAvailableCoachingDialog: false,

      tournamentPage: 1,
      tournamentTotal: 0,
      tournamentData: [],

      coachingList: [],
      coachingData: [],
    };
  },
  components: {
    IgButton,
    Dialog,
    AvailableTournamentVertical,
    CoachingList,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    async getTournaments(page) {
      var url = this.apiUrl + "/tournament/?page=" + page;

      var response = await axios.get(url);
      if (response.data) {
        this.tournamentPage = page;
        this.tournamentTotal = response.data.count;
        if (page == 1) this.tournamentData = [];
        this.tournamentData = this.tournamentData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getTournaments(1);
    var url = this.apiUrl + "/coach/?game_uuid=" + this.$route.params.id;
    var response = await axios.get(url);
    if (response.data) {
      this.coachingData = response.data;
      for (let i = 0; i < 4; i++) {
        if (this.coachingData[i]) {
          this.coachingList.push(this.coachingData[i]);
        }
      }
    }
  },
});
</script>
