
import { defineComponent } from "vue";
import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import IgDatePicker from "@/components/baseUI/IgDatePicker.vue";
import { emitter } from "@/main";
export default defineComponent({
  data() {
    return {
      selectedStatusName: "",
      statusData: [
        { name: "Upcoming", selected: false },
        { name: "Ongoing", selected: false },
        { name: "Previous", selected: false },
      ],

      startDate: "",
      endDate: "",

      selectedRegionName: "",
      regionData: [
        { name: "India", selected: false },
        { name: "USA", selected: false },
      ],

      selectedTeamSizeName: "",
      teamSizeData: [
        { name: "5 Member", selected: false },
        { name: "10 Member", selected: false },
        { name: "15 Member", selected: false },
      ],
      min_fee: "",
      max_fee: "",

      applyFilter: false,
    };
  },
  components: {
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
    IgButton,
    IgDatePicker,
  },
  computed: {
    checkFilterApply() {
      if (
        this.selectedStatusName != "" ||
        this.selectedRegionName != "" ||
        this.selectedTeamSizeName != "" ||
        this.startDate != "" ||
        this.endDate != ""
      ) {
        return true;
      }
      return false;
    },
  },
  methods: {
    selectStatus(index) {
      this.statusData.forEach((element) => {
        element.selected = false;
      });
      this.statusData[index].selected = true;
      this.selectedStatusName = this.statusData[index].name;
    },
    selectRegion(index) {
      this.regionData.forEach((element) => {
        element.selected = false;
      });
      this.regionData[index].selected = true;
      this.selectedRegionName = this.regionData[index].name;
    },
    selectTeamSize(index) {
      this.teamSizeData.forEach((element) => {
        element.selected = false;
      });
      this.teamSizeData[index].selected = true;
      this.selectedTeamSizeName = this.teamSizeData[index].name;
    },
    cleanFilter() {
      this.applyFilter = false;
      this.selectedStatusName = "";
      this.statusData.forEach((element) => {
        element.selected = false;
      });
      this.selectedRegionName = "";
      this.regionData.forEach((element) => {
        element.selected = false;
      });
      this.selectedTeamSizeName = "";
      this.teamSizeData.forEach((element) => {
        element.selected = false;
      });
      this.min_fee = "";
      this.max_fee = "";
      this.startDate = "";
      this.endDate = "";
      emitter.emit("cleanDatePickerDate");
    },
    changeStartDate(date) {
      this.startDate = date;
    },
    changeEndDate(date) {
      this.endDate = date;
    },
  },
});
