<template>
  <div class="grid grid-cols-1">
    <div class="banner">
      <div class="md:flex w-100">
        <a href="javascript:void(0)" class="mr-8" @click="$router.push('/')">
          <svg
            width="20"
            height="18"
            viewBox="0 0 24 22"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M22.5575 9.28672C22.4593 9.27078 22.3599 9.26343 22.2605 9.26472H5.32893L5.69813 9.093C6.05901 8.92219 6.38731 8.68973 6.66834 8.40613L11.4164 3.6581C12.0417 3.06116 12.1468 2.10088 11.6654 1.38283C11.1051 0.617658 10.0306 0.45152 9.26537 1.01181C9.20355 1.0571 9.14479 1.10647 9.08957 1.15959L0.503633 9.74553C-0.167359 10.4158 -0.167949 11.503 0.502291 12.174C0.502721 12.1744 0.503204 12.1749 0.503633 12.1754L9.08957 20.7613C9.7611 21.4309 10.8484 21.4294 11.5181 20.7579C11.5708 20.7051 11.62 20.6488 11.6654 20.5896C12.1468 19.8715 12.0417 18.9112 11.4164 18.3143L6.67693 13.5577C6.42498 13.3055 6.13531 13.094 5.81833 12.9309L5.30317 12.6991H22.166C23.0432 12.7317 23.8128 12.1189 23.9776 11.2567C24.1294 10.3205 23.4936 9.43859 22.5575 9.28672Z"
              fill="white"
            />
          </svg>
        </a>
        <div class="w-100">
          <div class="md:flex">
            <div class="md:flex">
              <div class="my-auto user-image">
                <img
                  v-if="profilePicture != null"
                  :src="profilePicture"
                  class="rounded-xl"
                />
                <img v-else src="@/assets/images/user.png" class="rounded-xl" />
              </div>
              <div class="pl-6 mt-3 md:my-auto">
                <p class="text-3xl truncate mb-2">
                  <b>{{ name }}</b>
                </p>
                <p class="color-gray">
                  <b
                    >{{ $t("userPage.offline") }} <span>&#8226;</span>
                    {{ $t("userPage.joined_since") }}
                    {{ displayDate(created) }}</b
                  >
                </p>
              </div>
            </div>
            <div class="sm:flex ml-auto mt-5 md:my-auto">
              <IgButton
                class="ig-auto px-10"
                buttonName="add_friend"
                textColor="ig-white-text"
                backgroundColor="ig-purple"
              />
              <IgButton
                class="ig-auto px-10 mt-3 sm:mt-0 ml-3"
                buttonName="message"
                textColor="ig-black-text"
                backgroundColor="ig-white"
              />
            </div>
          </div>
          <div
            class="grid grid-flow-col auto-cols-max gap-10 mt-6"
            v-if="played != null || winRate != null || reputation != null"
          >
            <div class="text-center">
              <p class="color-gray">{{ $t("userPage.played") }}</p>
              <p>
                <b>{{ played + " " + $t("userPage.time") }}</b>
              </p>
            </div>
            <div class="text-center">
              <p class="color-gray">{{ $t("userPage.win_rate") }}</p>
              <p>
                <b>{{ winRate + "%" }}</b>
              </p>
            </div>
            <div class="text-center">
              <p class="color-gray">{{ $t("userPage.reputation") }}</p>
              <p>
                <b>{{ reputation }}</b>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="tab mt-10 md:px-8">
    <div class="md:flex">
      <IgButton
        class="ig-small ig-auto px-3 mb-3 mx-3"
        buttonName="overview"
        textColor="ig-white-text"
        :backgroundColor="selectedTab == 'overview' ? 'ig-gray' : ''"
        SmallFontSize="ig-font-small"
        @click="changeTab('overview')"
      />
      <IgButton
        class="ig-small ig-auto px-3 mb-3 mx-3"
        buttonName="statistics"
        textColor="ig-white-text"
        :backgroundColor="selectedTab == 'statistics' ? 'ig-gray' : ''"
        SmallFontSize="ig-font-small"
        @click="changeTab('statistics')"
      />
      <IgButton
        class="ig-small ig-auto px-3 mb-3 mx-3"
        buttonName="team"
        textColor="ig-white-text"
        :backgroundColor="selectedTab == 'team' ? 'ig-gray' : ''"
        SmallFontSize="ig-font-small"
        @click="changeTab('team')"
      />
      <IgButton
        class="ig-small ig-auto px-3 mb-3 mx-3"
        buttonName="friends"
        textColor="ig-white-text"
        :backgroundColor="selectedTab == 'friends' ? 'ig-gray' : ''"
        SmallFontSize="ig-font-small"
        @click="changeTab('friends')"
      />
      <IgButton
        class="ig-small ig-auto px-3 mb-3 mx-3"
        buttonName="spaces"
        textColor="ig-white-text"
        :backgroundColor="selectedTab == 'spaces' ? 'ig-gray' : ''"
        SmallFontSize="ig-font-small"
        @click="changeTab('spaces')"
      />
      <IgButton
        class="ig-small ig-auto px-3 mb-3 mx-3"
        buttonName="tournaments"
        textColor="ig-white-text"
        :backgroundColor="selectedTab == 'tournaments' ? 'ig-gray' : ''"
        SmallFontSize="ig-font-small"
        @click="changeTab('tournaments')"
      />
    </div>
  </div>
  <div class="tab-view mt-8 px-4 md:px-8 pb-5 h-full">
    <OverView
      v-if="selectedTab == 'overview'"
      :description="description"
      :gameAccount="gameAccount"
    />
    <Statistics v-if="selectedTab == 'statistics'" />
    <Team v-if="selectedTab == 'team'" :data="team" />
    <Friends v-if="selectedTab == 'friends'" />
    <Spaces v-if="selectedTab == 'spaces'" />
    <Tournaments v-if="selectedTab == 'tournaments'" />
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import OverView from "@/components/user/ovierview.vue";
import Statistics from "@/components/user/statistics.vue";
import Team from "@/components/user/team.vue";
import Friends from "@/components/user/friends.vue";
import Spaces from "@/components/user/spaces.vue";
import Tournaments from "@/components/user/tournaments.vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import axios from "axios";
import { mapState } from "vuex";

export default defineComponent({
  data() {
    return {
      selectedTab: "overview",
      name: "",
      profilePicture: null,
      played: null,
      winRate: null,
      reputation: null,
      description: null,
      gameAccount: [],
      created: null,
      team: [],
    };
  },
  components: {
    OverView,
    Statistics,
    Team,
    Friends,
    Spaces,
    Tournaments,
    IgButton,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    displayDate(value) {
      if (value == null || value == "") return "";

      return new Date(value).toLocaleString("en-GB", {
        day: "2-digit",
        month: "long",
        year: "numeric",
      });
    },
    changeTab(key: string) {
      this.selectedTab = key;
    },
  },
  async created() {
    var response = await axios.get(this.apiUrl + "/profile", {
      headers: {
        authorization: "Bearer " + localStorage.getItem("access_token"),
      },
    });
    if (response.data) {
      this.name = response.data.full_name;
      this.profilePicture = response.data.profile_picture;
      this.played = response.data.played;
      this.winRate = response.data.winRate;
      this.reputation = response.data.reputation;
      this.description = response.data.description;
      this.gameAccount = response.data.game_account;
      this.created = response.data.created;
      this.team = response.data.team;
    }
  },
});
</script>
