<template>
  <div class="grid grid-cols-1 md:grid-cols-2 gap-10 statistics">
    <div class="flex bg-black" v-for="(item, index) in statistics" :key="index">
      <div class="statistic-img-bock">
        <img :src="item.thumb" class="w-full" />
      </div>
      <div class="statistic-block w-full">
        <p class="truncate">{{ name }}</p>
        <button>{{ $t("teamPage.tier_lll") }}</button>
        <div class="flex justify-between">
          <div>
            <small class="color-gray">{{ $t("teamPage.played") }}</small>
            <p>
              <b>{{ item.played }}</b>
            </p>
          </div>
          <div>
            <small class="color-gray">{{ $t("teamPage.played_rate") }}</small>
            <p>
              <b>{{ item.play_rate }}</b>
            </p>
          </div>
          <div>
            <small class="color-gray">{{ $t("teamPage.win_rate") }}</small>
            <p>
              <b>{{ item.win_rate }}</b>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import axios from "axios";
import { mapState } from "vuex";
export default defineComponent({
  data() {
    return {
      statistics: [],
    };
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {},
  async created() {
    var response = await axios.get(
      this.apiUrl + "/teams/" + this.$route.params.id + "/statistics"
    );
    if (
      response.data &&
      response.data.results &&
      response.data.results.length > 0
    ) {
      this.statistics = response.data.results;
    }
  },
});
</script>
