<template>
  <div class="grid grid-cols-1 xl:grid-cols-10 gap-10 px-2 xl:px-0">
    <div class="xl:col-span-6">
      <div class="flex mb-10">
        <div class="serch-input">
          <input
            type="text"
            placeholder="Search tournament"
            v-model="keyword"
            @input="getSearchData()"
          />
          <a
            href="javascript:void(0)"
            v-if="keyword != ''"
            @click="cleanSearchData()"
          >
            <svg
              width="0.8rem"
              height="0.8rem"
              viewBox="0 0 16 16"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M15.6548 1.40965L14.5581 0.325903C14.2613 0.029129 13.771 0.029129 13.4613 0.325903L7.99032 5.79674L2.41626 0.222677C2.11935 -0.0742258 1.62903 -0.0742258 1.31948 0.222677L0.222708 1.31945C-0.0741953 1.61623 -0.0741953 2.10655 0.222708 2.41623L5.784 7.97752L0.325934 13.4614C0.0291595 13.7582 0.0291595 14.2485 0.325934 14.5582L1.42271 15.6549C1.71948 15.9517 2.2098 15.9517 2.51948 15.6549L7.99032 10.184L13.4613 15.6549C13.7581 15.9517 14.2484 15.9517 14.5581 15.6549L15.6548 14.5582C15.9516 14.2614 15.9516 13.7711 15.6548 13.4614L10.171 7.99042L15.6419 2.51958C15.9516 2.20965 15.9516 1.71932 15.6548 1.40965Z"
                fill="white"
              />
            </svg>
          </a>
        </div>
        <button class="btn-add ml-auto px-3 py-3 text-center">
          <svg
            width="1rem"
            height="1rem"
            aria-hidden="true"
            focusable="false"
            data-prefix="fas"
            data-icon="plus"
            class="svg-inline--fa fa-plus fa-w-14"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 448 512"
          >
            <path
              fill="currentColor"
              d="M416 208H272V64c0-17.67-14.33-32-32-32h-32c-17.67 0-32 14.33-32 32v144H32c-17.67 0-32 14.33-32 32v32c0 17.67 14.33 32 32 32h144v144c0 17.67 14.33 32 32 32h32c17.67 0 32-14.33 32-32V304h144c17.67 0 32-14.33 32-32v-32c0-17.67-14.33-32-32-32z"
            ></path>
          </svg>
        </button>
      </div>
      <div v-if="tournamentData.length > 0">
        <AvailableTournamentHorizontal
          v-for="(item, index) in tournamentData"
          :key="index"
          :data="item"
        />
      </div>
      <div v-else class="text-center" style="margin: 10rem; 0rem">
        <p>
          <b>{{
            $t("gamePage.no_available_tournaments_with_those_filters")
          }}</b>
        </p>
        <small class="color-gray">{{
          $t("gamePage.please_try_again_in_sometimes")
        }}</small>
      </div>
      <div
        class="mt-6 text-center"
        v-if="tournamentData.length < tournamentTotal"
      >
        <IgButton
          class="ig-small px-3 ig-font-small ig-add-border"
          buttonName="see_more"
          textColor="ig-white-text"
          @click="getGame(gamePage + 1)"
        />
      </div>
    </div>
    <div class="xl:col-span-4">
      <IgFilter />
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from "vue";
import IgFilter from "@/components/baseUI/IgFilter.vue";
import AvailableTournamentHorizontal from "@/components/availableTournament/AvailableTournamentHorizontal.vue";
import axios from "axios";
import { mapState } from "vuex";
export default defineComponent({
  data() {
    return {
      keyword: "",

      tournamentPage: 1,
      tournamentTotal: 0,
      tournamentData: [],
    };
  },
  components: {
    IgFilter,
    AvailableTournamentHorizontal,
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    getSearchData() {
      if (this.keyword.length % 3 == 0) {
        this.getTournament(1);
      }
    },
    cleanSearchData() {
      this.keyword = "";
      this.getTournament(1);
    },
    async getTournament(page) {
      var response = await axios.get(this.apiUrl + "/tournament/", {
        params: {
          page: page,
          keyword: this.keyword,
        },
      });
      if (response.data != null && response.data.results) {
        this.tournamentPage = page;
        this.tournamentTotal = response.data.count;
        if (page == 1) this.tournamentData = [];
        this.tournamentData = this.tournamentData.concat(response.data.results);
      }
    },
  },
  async created() {
    this.getTournament(1);
  },
});
</script>
